.navbar {
    background-color: #4CAF50;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}

.navbar-logo {
    font-size: 1.5rem;
    font-weight: bold;
}

.navbar-links a {
    margin: 0 1rem;
    color: white;
    text-decoration: none;
    font-weight: bold;
}

.navbar-links a:hover {
    text-decoration: underline;
}
